@import url("https://fonts.googleapis.com/css2?family=Open+Sans:ital,wdth,wght@0,75..100,300..800;1,75..100,300..800&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

img {
  max-height: 100%;
  vertical-align: middle;
}

ul {
  list-style: none;
}

body {
  font-family: "Open Sans", serif;
  font-size: 14px;
  line-height: 22px;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: 600;
}

.container,
.container-fluid {
  width: 100%;
  margin: 0 auto;
  padding: 0 15px;
}
.container .row,
.container-fluid .row {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -15px;
}

